import React from 'react'
import './style.css'

function StoryCircle(props) {
    const {
        data,
        showTitle,
    } = props
    return (
        <>
            <a href="0#" className="circle-story">
                <div className="circle">
                    <span></span>
                    <span></span>
                    <span></span>
                    <span></span>
                    <img src="data:image/png;base64," alt="image" />
                </div>
                {showTitle ?
                    <div className="title">
                        <span>{data.name}</span>
                    </div> : null
                }

            </a>
        </>
    )
}

export default StoryCircle;